@import "./../../tabs-block.scss";

.product-page {
  .current-page-info {
    &__container {
      .current-page-info__page-name {
        color: $gray;
        &:hover {
        }
        &:active {
        }
        &:focus {
        }
      }
    }
  }

  .product-section {
    margin-top: 130px;
    &__container {
      display: flex;
      justify-content: space-between;
      .swiper-container.product-section__swiper-block {
        padding-top: 76px;
        padding-bottom: 64px;
        background-color: #fcfcfc;
        margin-top: 6px;
        position: relative;
        max-width: 553px;
        max-height: 571px;
        height: 100%;
        width: 100%;
        border: 1px solid $green;
        overflow: hidden;
        .swiper-wrapper.product-item__wrapper {
          height: 100%;
          width: 100%;
        }
        .swiper-button-prev {
          color: $darkGreen;
          margin-top: 1px;
          margin-left: 6px;
          width: 23px;
          height: 23px;
          &::after {
            content: "\e919";
            font-family: icomoon;
            font-size: 23px;
            transform: rotate(180deg);
          }
        }
        .swiper-button-next {
          margin-top: 1px;
          margin-right: 6px;
          color: $darkGreen;
          width: 23px;
          height: 23px;
          &::after {
            content: "\e919";
            font-family: icomoon;
            font-size: 23px;
          }
        }
        .swiper-slide.product-item__slide {
        }
        .swiper-slide {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 100%;
          height: 100%;
          img {
            img {
              width: 100%;
              height: 100%;
              object-fit: contain;
            }
          }
        }
      }
      .text-block-product-section {
        max-width: 660px;
        width: 100%;
        &__sub-title {
          color: $darkGreen;
          @include rale-700-18;
        }
        &__sub-title-block {
          display: flex;
          justify-content: space-between;
          align-items: center;
        }
        &__code {
          color: $gray;
          @include mont-500-16;
        }
        &__title {
          @include rale-800-30;
          margin-top: 17px;
          max-width: 450px;
        }
        &__info-blocks {
          margin-top: 44px;
          display: flex;
          flex-direction: column;
          gap: 20px;
        }
        .attention-block {
          padding: 20px 17px 20px 16px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          background-color: #fcfcfc;
          border: 1px solid rgba($color: #000000, $alpha: 0.08);
          margin-top: 30px;
          &::before {
            font-family: "icomoon";
            content: "\e902";
            display: flex;
            justify-content: center;
            align-items: center;
            width: 28px;
            height: 28px;
            color: $white;
            background-color: $green;
            border-radius: 50%;
          }
          &__text {
            @include mont-500-15;
            letter-spacing: 0;
            max-width: 570px;
          }
        }
        .price-block-product-section {
          margin-top: 34px;
          display: flex;
          align-items: center;
          justify-content: flex-end;
          &__title {
            @include mont-600-18;
          }
          &__price {
            color: $darkGreen;
            @include mont-600-24;
            margin-left: 7px;
            .product-item__price-valute {
              @include mont-600-16;
              margin-left: 5px;
            }
          }
          .old-price {
            position: relative;
            color: $gray;
            margin-left: 10px;
            @include mont-500-18;
            .product-item__price-valute {
              @include mont-500-14;
              margin-left: 4px;
            }
            &::after {
              content: "";
              position: absolute;
              left: 0;
              top: 55%;
              width: 100%;
              height: 1px;
              background-color: $gray;
            }
          }
        }
        .info-block-product-section {
          display: flex;
          justify-content: space-between;
          &__type-info {
            @include rale-700-16;
          }
          &__info-text {
            color: $gray;
            @include mont-500-15;
            max-width: 368px;
            margin-right: 20px;
            width: 100%;
          }
          &:nth-child(2) {
            margin-top: 1px;
          }
          &:nth-child(3) {
            margin-top: 16px;
          }
          &:nth-child(4) {
            margin-top: 16px;
          }
        }
        .rectangle.background {
          padding: 17px 60px 17px 65px;
          margin-left: 32px;
          &:hover {
          }
          &:active {
          }
          &:focus {
          }
        }
      }
    }
  }
}
